<template>
  <body>
    <div id="backgroundTriangle"></div>
    <HeaderLogo />
    <Notification />
    <header id="headerLeft">
      <Navigation />
      <UserCard />
    </header>
    <main id="mainProfileSection">
      <section id="profileSection">
        <div id="profileSectionContent">
          <h1 class="titles">Profil</h1>
          <div id="profileSectionOverview">
            <div id="profileSectionOverviewDetails">
              <div>
                <img
                  class="staticImg"
                  src="../assets/userAvatar.png"
                  alt="User Avatar"
                />
                <h1>{{ accountStore.userNickname }}</h1>
              </div>
              <span>{{
                `Konto stworzone: ${accountStore.userCreatedAt}`
              }}</span>
            </div>
          </div>
          <div id="profileSectionDetails">
            <div id="profileUserAvatarContainer">
              <div
                id="profileUserAvatar"
                @mouseenter="profileUserImgShow"
                @mouseleave="profileUserImgHide"
              >
                <img
                  class="staticImg"
                  src="../assets/userAvatar.png"
                  alt="User Avatar"
                />
                <div
                  v-if="isEditButtonDisplay"
                  id="profileUserAvatarOverlay"
                  class="pointer"
                  title="Zmień zdjęcie"
                >
                  <svg-icon
                    id="profileUserEditIcon"
                    type="mdi"
                    :path="iconsStore.mdiPencilOutline"
                  ></svg-icon>
                </div>
              </div>
              <div id="profileUserButtons">
                <div title="Zresetuj zdjęcie" class="pointer">
                  <svg-icon
                    id="profileUserEditIcon"
                    type="mdi"
                    :path="iconsStore.mdiImageRefreshOutline"
                  ></svg-icon>
                  Zresetuj zdjęcie
                </div>
                <div title="Zmień zdjęcie" class="pointer">
                  <svg-icon
                    id="profileUserEditIcon"
                    type="mdi"
                    :path="iconsStore.mdiImagePlusOutline"
                  ></svg-icon>
                  Zmień zdjęcie
                </div>
              </div>
            </div>
            <div id="profileUserPassword">
              <div id="profileUserPasswordHeader">
                <h1 id="profileUserPasswordTitle">Zmień hasło</h1>
                <button
                  @click="changePassword()"
                  id="profileUserPasswordButton"
                  :disabled="isButtonDisabled"
                >
                  Zmień
                </button>
              </div>
              <div class="formInput formItem">
                <label for="oldPassInput">
                  Stare hasło
                  <span class="formError">{{ errors.oldPassError }}</span>
                </label>
                <div :class="oldPassClass" class="border">
                  <input
                    name="oldPassInput"
                    :type="oldPasswordType"
                    v-model="oldPassInput"
                    maxlength="20"
                  />
                  <div class="helpBox" title="Wpisz stare hasło">
                    <svg-icon
                      @mouseenter="oldPasswordType = 'text'"
                      @mouseleave="oldPasswordType = 'password'"
                      type="mdi"
                      :class="oldPassClass"
                      :path="oldPassIcon"
                    ></svg-icon>
                  </div>
                </div>
              </div>
              <div class="formInput formItem">
                <label for="newPassInput">
                  Nowe hasło
                  <span class="formError">{{ errors.newPassError }}</span>
                </label>
                <div :class="newPassClass" class="border">
                  <input
                    name="newPassInput"
                    :type="newPasswordType"
                    v-model="newPassInput"
                    maxlength="20"
                  />
                  <div class="helpBox" title="Wpisz nowe hasło">
                    <svg-icon
                      @mouseenter="newPasswordType = 'text'"
                      @mouseleave="newPasswordType = 'password'"
                      type="mdi"
                      :class="newPassClass"
                      :path="newPassIcon"
                    ></svg-icon>
                  </div>
                </div>
              </div>
            </div>
            <div id="profileUserRoles">
              <h1 id="profileUserRolesTitle">Twoje rangi</h1>
              <div id="profileUserRolesContainer">
                <div
                  class="roleBadge"
                  v-for="role in accountStore.userRoles"
                  :key="role"
                  :class="role"
                >
                  {{ role }}
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section id="preferencesSection">
        <div id="preferencesSectionContent">
          <h1 class="titles">Preferencje</h1>
          <ul id="preferencesSectionList">
            <li
              class="preferenceElement"
              v-for="preference in preferenceStore.preferences"
              :key="preference.name"
            >
              <svg-icon
                class="preferenceElementIcon"
                type="mdi"
                :path="preference.icon"
              ></svg-icon>
              <h1 class="preferenceElementTitle">
                {{ preference.label }}
              </h1>
              <div
                @click="
                  preferenceStore.setPreference(
                    preference.name,
                    undefined,
                    true
                  )
                "
                class="preferenceElementState pointer"
              >
                {{ preference.state ? "Włącz" : "Wyłącz" }}
              </div>
            </li>
          </ul>
        </div>
      </section>
    </main>
  </body>
</template>

<script>
import API from "../api";
import svgIcon from "@jamescoyle/vue-icon";
import popupSound from "../assets/audio/popup.wav";

import { useAlertsStore } from "../stores/alertsStore";
import { useAccountStore } from "../stores/accountStore";
import { usePreferenceStore } from "../stores/preferenceStore";
import { useIconsStore } from "../stores/iconsStore";

import HeaderLogo from "../components/HeaderLogo.vue";
import Notification from "../components/Assets/Notification.vue";
import UserCard from "../components/Assets/UserCard.vue";
import Navigation from "../components/Dashboard/MainNavigation.vue";

export default {
  name: "ProfileView",
  components: {
    svgIcon,
    HeaderLogo,
    Notification,
    UserCard,
    Navigation,
  },
  setup() {
    const accountStore = useAccountStore();
    const preferenceStore = usePreferenceStore();
    const alertsStore = useAlertsStore();
    const iconsStore = useIconsStore();

    return { accountStore, alertsStore, iconsStore, preferenceStore };
  },
  data() {
    return {
      isButtonDisabled: true,
      isEditButtonDisplay: false,
      oldPassIcon: this.iconsStore.questionMarkIcon,
      newPassIcon: this.iconsStore.questionMarkIcon,
      oldPassInput: "",
      newPassInput: "",
      oldPassClass: "",
      newPassClass: "",
      oldPasswordType: "password",
      newPasswordType: "password",
      errors: {
        oldPassError: "",
        newPassError: "",
      },
    };
  },
  methods: {
    async changePassword() {
      if (this.oldPassInput.length < 3 && this.newPassInput.length < 3) return;

      const formValues = {
        nick: this.accountStore.userNickname,
        oldPassword: this.oldPassInput,
        newPassword: this.newPassInput,
      };

      const response = await API.changePassword(formValues);

      this.oldPassInput = "";
      this.newPassInput = "";

      await this.alertsStore.showNotification(
        response.duration,
        response.status,
        response.message
      );

      if (this.preferenceStore.preferences[0].state) {
        const notificationSound = new Audio(popupSound);
        notificationSound.volume = 0.5;
        notificationSound.play();
      }

      if (response.status === "success") {
        this.oldPassIcon = this.iconsStore.questionMarkIcon;
        this.newPassIcon = this.iconsStore.questionMarkIcon;
        this.oldPassClass = "neutral";
        this.newPassClass = "neutral";
        this.errors.oldPassError = "";
        this.errors.newPassError = "";

        this.isButtonDisabled = true;
      }
    },
    profileUserImgShow() {
      //this.isEditButtonDisplay = true;
    },
    profileUserImgHide() {
      //this.isEditButtonDisplay = false;
    },
    formVerification(value) {
      if (value.length >= 3)
        return {
          icon: this.iconsStore.successIcon,
          status: "success",
          error: "",
        };
      else if (value.length > 0 && value.length < 3)
        return {
          icon: this.iconsStore.alertCircleIcon,
          status: "alert",
          error: "Minimum 3 znaki!",
        };
      else
        return {
          icon: this.iconsStore.alertCircleIcon,
          status: "alert",
          error: "Puste pole!",
        };
    },
    buttonVerification() {
      if (this.oldPassInput.length < 3 || this.newPassInput.length < 3) {
        this.isButtonDisabled = true;
      } else {
        this.isButtonDisabled = false;
      }
    },
  },
  watch: {
    oldPassInput() {
      this.buttonVerification();
      const data = this.formVerification(this.oldPassInput);
      this.oldPassIcon = data.icon;
      this.oldPassClass = data.status;
      this.errors.oldPassError = data.error;
    },
    newPassInput() {
      this.buttonVerification();
      const data = this.formVerification(this.newPassInput);
      this.newPassIcon = data.icon;
      this.newPassClass = data.status;
      this.errors.newPassError = data.error;
    },
  },
};
</script>

<style lang="scss" scoped>
body {
  background-color: #20222a;

  #mainProfileSection {
    position: absolute;
    bottom: 0;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    width: 100%;
    height: 75%;

    #profileSection {
      width: 30%;

      #profileSectionOverview {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        width: 600px;
        height: 100px;
        border-radius: 5px;
        background: linear-gradient(to bottom right, #20232a, #1a1c23);
        box-shadow: 0 4px 4px #181920;

        #profileSectionOverviewDetails {
          position: relative;
          top: 25px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          width: 550px;
          height: 60px;
          padding: 0 20px;
          border-radius: 5px;
          background: linear-gradient(to top right, #20232a, #1a1c23);
          box-shadow: 0 4px 4px #181920;

          div {
            display: flex;
            flex-flow: row nowrap;
            align-items: center;
          }

          img {
            width: 40px;
            height: 40px;
            border-radius: 5px;
            margin-right: 10px;
          }

          span {
            background: linear-gradient(to right, #4d4e56, #49505e);
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
          }
        }
      }

      #profileSectionDetails {
        display: grid;
        grid-template-columns: repeat(2, 50%);
        grid-template-rows: auto 100px;
        width: 600px;
        height: 400px;
        margin-top: 50px;
        padding: 20px;
        background: linear-gradient(to top right, #20232a, #1a1c23);
        box-shadow: 0 4px 4px #181920;

        #profileUserAvatarContainer {
          position: relative;
          display: flex;
          justify-content: space-between;
          align-items: center;
          flex-flow: column nowrap;
          grid-column: 1 / 2;
          grid-row: 1 / 2;
          padding: 28px 0;

          #profileUserAvatar {
            position: relative;
            width: 100px;
            height: 100px;

            div {
              position: relative;
              width: 100px;
              height: 100px;
            }
          }

          img {
            position: absolute;
            display: block;
            width: 100px;
            height: 100px;
            border-radius: 10px;
          }

          #profileUserButtons {
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            font-size: 12px;
            padding: 0 5px;
            border: 1px solid #282c35;
            border-radius: 5px;
            box-shadow: 0 4px 4px #000;
            transition: 0.2s ease-in-out;

            svg {
              width: 24px;
              height: 24px;
              margin-right: 1px;
            }

            div {
              display: flex;
              align-items: center;
              margin: 5px 5px;

              &:hover {
                color: #666;
              }
            }
          }

          #profileUserAvatarOverlay {
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: #1a1c23;
            opacity: 0.5;
          }
        }

        #profileUserPassword {
          grid-column: 2 / 3;
          grid-row: 1 / 2;
          height: fit-content;
          margin: auto;
          padding: 0 15px 10px;
          border: 1px solid #282c35;
          border-radius: 5px;
          box-shadow: 0 4px 4px #000;

          .formItem {
            margin: 10px 0;

            .border {
              padding: 2px 5px;

              input {
                font-size: 18px;
              }
            }
          }

          #profileUserPasswordHeader {
            display: flex;
            flex-flow: row nowrap;
            justify-content: space-between;
            align-items: center;

            #profileUserPasswordTitle {
              margin: 10px 0;
              text-align: start;
            }

            #profileUserPasswordButton {
              padding: 1px 10px;
              color: #0270d7;
              border: 1px solid #0270d7;
              border-radius: 5px;
              background: transparent;
              transition: 0.2s ease-in-out;

              &:hover {
                background-color: #002d58;
              }

              &:disabled {
                color: #053664;
                border: 1px solid #053664;
                background-color: transparent;
                cursor: default;
              }
            }
          }
        }

        #profileUserRoles {
          grid-column: 1 / 3;
          grid-row: 2 / 3;

          #profileUserRolesTitle {
            text-align: start;
            margin: 10px 0;
          }

          #profileUserRolesContainer {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            width: 100%;
            height: 50px;
            padding: 0 10px;
            border: 1px solid #282c35;
            border-radius: 5px;
            box-shadow: 0 4px 4px #000;
          }
        }
      }
    }

    #preferencesSection {
      width: 25%;

      #preferencesSectionList {
        height: 540px;
        padding: 25px;
        border-radius: 5px;
        box-shadow: 0 4px 4px #181920;
        background: linear-gradient(to bottom right, #20232a, #1a1c23);

        .preferenceElement {
          display: flex;
          flex-flow: row nowrap;
          align-items: center;
          width: 100%;
          height: 50px;
          list-style: none;
          margin: 10px 0;
          padding: 10px;
          border-radius: 5px;
          background: linear-gradient(to bottom right, #23272e, #23272e05);

          svg {
            width: 10%;
            height: 30px;
          }

          h1 {
            width: 70%;
          }

          .preferenceElementState {
            width: 20%;
            height: 20px;
            display: flex;
            justify-content: center;
            align-items: center;
            border: 2px solid #d0d0d0;
            border-radius: 5px;
            transition: 0.1s ease-in-out;
            user-select: none;

            &:hover {
              color: #999;
              border-color: #999;
            }
          }
        }
      }
    }
  }
}
</style>